@import './src/common/CommonStyles.module';

.background {
  padding: 5px 0 5px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: black;
  opacity: 0.35;
  z-index: 20;
}

.modal {
  @include displayFlex(column, nowrap, space-between, '');
  position: fixed;
  width: 400px;
  height: 230px;
  left: 35%;
  top: 35%;
  padding: 10px;
  background: #F9F9FE;
  border-radius: 2px;
  z-index: 21;
}